import { useEffect, useRef, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Button, Progress, Alert } from 'reactstrap';
import io from "socket.io-client";
import { getSeats, getRequests, loadSeats, loadSeatsRequest } from '../../../redux/seatsRedux';
import './SeatChooser.scss';

const SeatChooser = ({ chosenDay, chosenSeat, updateSeat }) => {
  const dispatch = useDispatch();
  const seats = useSelector(getSeats);
  const requests = useSelector(getRequests);
  const socketRef = useRef();
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const socket = io("http://localhost:8000");
    socketRef.current = socket;

    socket.on("connect_error", () => {
      console.log("Failed to connect to server");
    });

    dispatch(loadSeatsRequest());

    socket.on("updateSeats", (seats) => {
      dispatch(loadSeats(seats));
      setIsLoading(false);
    });

    return () => {
      socket.disconnect();
    };
  }, [dispatch]);

  const isTaken = (seatId) => {
    return seats.some((item) => item.seat === seatId && item.day === chosenDay);
  };

  const prepareSeat = (seatId) => {
    if (seatId === chosenSeat)
      return (
        <Button key={seatId} className="m-1" disabled>
          {seatId}
        </Button>
      );
    else if (isTaken(seatId))
      return (
        <Button key={seatId} className="m-1" disabled outline>
          {seatId}
        </Button>
      );
    else
      return (
        <Button
          key={seatId}
          className="m-1"
          color="primary"
          onClick={(e) => updateSeat(e, seatId)}
          outline
        >
          {seatId}
        </Button>
      );
  };

  return (
    <div className="seat-chooser">
      {isLoading && <Progress animated className="mb-5" color="primary" value={75} />}
      {requests['LOAD_SEATS'] && requests['LOAD_SEATS'].error && !isLoading && <Alert color="danger">{requests['LOAD_SEATS'].error}</Alert>}
      {seats && (
        <div className="d-flex flex-wrap">
          {seats.filter((seat) => seat.day === chosenDay).map((seat) => prepareSeat(seat.seat))}
        </div>
      )}
    </div>
  );
};

export default SeatChooser;